module BP3D.Floorplanner {
  /** */
  export const floorplannerModes = {
    MOVE: 0,
    DRAW: 1,
    DELETE: 2
  };

  // Interaction styles
  // - Corner hover: Show target
  // - Corner select: N/A
  // - Corner deselect: N/A
  // - Label hover: Expand width
  // - Label select: Gapped outline
  // - Label deselect: Faded outline
  // - Wall hover: Expand width
  // - Wall select: Change color
  // - Wall deselect: Fade color
  // - Terminate wall: Change color

  // grid parameters
  const gridSpacing = 20; // pixels
  const gridWidth = 1;
  const gridColor = "#f1f1f1";

  // room config
  const roomColor = "#f9f9f9";

  // wall config
  const wallWidth = 5;
  const wallWidthHover = 7;
  const wallColor = "#dddddd"
  const wallColorHover = "#008cba"
  const edgeColor = "#888888"
  const edgeColorHover = "#008cba"
  const edgeWidth = 1
  const textLabelHoverStroke = 5

  const deselectColor = "#90b6d9";
  const deleteColor = "#ff0000";
  const terminateWallColor = "#000000";

  // corner config
  const cornerRadius = 0
  const cornerRadiusHover = 7
  const cornerColor = "#cccccc"
  const cornerColorHover = "#008cba"

  /**
   * The View to be used by a Floorplanner to render in/interact with.
   */
  export class FloorplannerView {

    /** The canvas element. */
    private canvasElement: HTMLCanvasElement;

    /** The 2D context. */
    private context;

    /** */
    constructor(private floorplan: Model.Floorplan, private viewmodel: Floorplanner, private canvas: string) {
      this.canvasElement = <HTMLCanvasElement>document.getElementById(canvas);
      this.context = this.canvasElement.getContext('2d');

      var scope = this;
      $(window).resize(() => {
        scope.handleWindowResize();
      });
      this.handleWindowResize();
    }

    /** */
    public handleWindowResize() {
      var canvasSel = $("#" + this.canvas);
      var parent = canvasSel.parent();
      canvasSel.height(parent.innerHeight());
      canvasSel.width(parent.innerWidth());
      this.canvasElement.height = parent.innerHeight();
      this.canvasElement.width = parent.innerWidth();
      this.draw();
    }

    /** */
    public draw() {
      this.context.clearRect(0, 0, this.canvasElement.width, this.canvasElement.height);

      this.drawGrid();

      this.floorplan.getRooms().forEach((room) => {
        this.drawRoom(room);
      })

      this.floorplan.getWalls().forEach((wall) => {
        this.drawWall(wall);
      });

      this.floorplan.getCorners().forEach((corner) => {
        this.drawCorner(corner);
      });

      if (this.viewmodel.mode == floorplannerModes.DRAW) {
        this.drawTarget(this.viewmodel.targetX, this.viewmodel.targetY, this.viewmodel.lastNode);
      }

      this.floorplan.getWalls().forEach((wall) => {
        this.drawWallLabels(wall);
      });

      if (this.viewmodel.overlay) {
        this.drawOverlay(this.viewmodel.overlay);
      }

      this.floorplan.getTextLabels().forEach((label) => {
        this.drawTextLabel(label);
      });
    }

    /** */
    private somethingActiveAndNotInSelected(): boolean {
      var model = this.viewmodel;
      if (model.activeCorner) {
        return true; // No selection possible for corners
      } else if (model.activeTextLabel) {
        return !model.selectedTextLabels.includes(model.activeTextLabel);
      } else if (model.activeWall) {
        return !model.selectedWalls.includes(model.activeWall);
      } else {
        return false; // Nothing was active
      }
    }

    /** */
    private drawWallLabels(wall: Model.Wall) {
      // we'll just draw the shorter label... idk
      if (wall.backEdge && wall.frontEdge) {
        if (wall.backEdge.interiorDistance < wall.frontEdge.interiorDistance) {
          this.drawEdgeLabel(wall.backEdge);
        } else {
          this.drawEdgeLabel(wall.frontEdge);
        }
      } else if (wall.backEdge) {
        this.drawEdgeLabel(wall.backEdge);
      } else if (wall.frontEdge) {
        this.drawEdgeLabel(wall.frontEdge);
      }
    }

    /** */
    private drawWall(wall: Model.Wall) {
      var activeWall = this.viewmodel.activeWall;
      var hover = (wall === activeWall);
      var selected = (this.viewmodel.selectedWalls && this.viewmodel.selectedWalls.includes(wall));
      var color = wallColor;
      if (hover && this.viewmodel.mode == floorplannerModes.DELETE) {
        color = deleteColor;
      } else if (hover) {
        color = wallColorHover;
      } else if (selected) {
        // If there's an active wall outside of the multiselect, then show deselect
        if (this.somethingActiveAndNotInSelected()) {
          color = deselectColor;
        // Otherwise, show normal hovercolor
        } else {
          color = wallColorHover;
        }
      }
      this.drawLine(
        this.viewmodel.convertX(wall.getStartX()),
        this.viewmodel.convertY(wall.getStartY()),
        this.viewmodel.convertX(wall.getEndX()),
        this.viewmodel.convertY(wall.getEndY()),
        hover ? wallWidthHover : wallWidth,
        color
      );
      if (!(hover || selected) && wall.frontEdge) {
        this.drawEdge(wall.frontEdge, hover);
      }
      if (!(hover || selected) && wall.backEdge) {
        this.drawEdge(wall.backEdge, hover);
      }
    }

    /** */
    private drawEdgeLabel(edge: Model.HalfEdge) {
      return;
      // var pos = edge.interiorCenter();
      // var length = edge.interiorDistance();
      // if (length < 60) {
      //   // dont draw labels on walls this short
      //   return;
      // }
      // this.context.font = "normal 12px Arial";
      // this.context.fillStyle = "#000000";
      // this.context.textBaseline = "middle";
      // this.context.textAlign = "center";
      // this.context.strokeStyle = "#ffffff";
      // this.context.lineWidth = 4;

      // this.context.strokeText(Core.Dimensioning.cmToMeasure(length),
      //   this.viewmodel.convertX(pos.x),
      //   this.viewmodel.convertY(pos.y));
      // this.context.fillText(Core.Dimensioning.cmToMeasure(length),
      //   this.viewmodel.convertX(pos.x),
      //   this.viewmodel.convertY(pos.y));
    }

    /** */
    private drawEdge(edge: Model.HalfEdge, hover) {
      var color = edgeColor;
      if (hover && this.viewmodel.mode == floorplannerModes.DELETE) {
        color = deleteColor;
      } else if (hover) {
        color = edgeColorHover;
      }
      var corners = edge.corners();

      var scope = this;
      this.drawPolygon(
        Core.Utils.map(corners, function (corner) {
          return scope.viewmodel.convertX(corner.x);
        }),
        Core.Utils.map(corners, function (corner) {
          return scope.viewmodel.convertY(corner.y);
        }),
        false,
        null,
        true,
        color,
        edgeWidth
      );
    }

    /** */
    private drawRoom(room: Model.Room) {
      var scope = this;
      this.drawPolygon(
        Core.Utils.map(room.corners, (corner: Model.Corner) => {
          return scope.viewmodel.convertX(corner.x);
        }),
        Core.Utils.map(room.corners, (corner: Model.Corner) =>  {
          return scope.viewmodel.convertY(corner.y);
        }),
        true,
        roomColor
      );
    }

    /** */
    private drawCorner(corner: Model.Corner) {
      var hover = (corner === this.viewmodel.activeCorner);
      var color = cornerColor;
      if (hover && this.viewmodel.mode == floorplannerModes.DELETE) {
        color = deleteColor;
      } else if (hover) {
        color = cornerColorHover;
      }
      this.drawCircle(
        this.viewmodel.convertX(corner.x),
        this.viewmodel.convertY(corner.y),
        hover ? cornerRadiusHover : cornerRadius,
        color
      );
    }

    /** */
    private drawTarget(x: number, y: number, lastNode) {
      // If we've started a line
      var targetColor = cornerColorHover;
      if (this.viewmodel.lastNode) {
        // If we're going to merge with a target, then change our color
        var tmpCorner = new Model.Corner(this.floorplan, x, y, 'tmpCornerId');
        tmpCorner.x = x;
        tmpCorner.y = y;
        if (tmpCorner.willMergeWithIntersected()) {
          targetColor = terminateWallColor;
        }

        // Draw our line + circle as per normal
        this.drawLine(
          this.viewmodel.convertX(lastNode.x),
          this.viewmodel.convertY(lastNode.y),
          this.viewmodel.convertX(x),
          this.viewmodel.convertY(y),
          wallWidthHover,
          wallColorHover
        );
      }
      // Draw our line + circle as per normal
      this.drawCircle(
        this.viewmodel.convertX(x),
        this.viewmodel.convertY(y),
        cornerRadiusHover,
        targetColor
      );
    }

    /** */
    private drawOverlay(overlay) {
      this.drawRectangle(
        this.viewmodel.convertX(overlay.startX),
        this.viewmodel.convertY(overlay.startY),
        this.viewmodel.convertX(overlay.endX),
        this.viewmodel.convertY(overlay.endY),
        true /* fill */,
        '#0000FF30',
        true /* stroke */,
        '#0000FF60',
        2
      );
    }

    public setTextLabelStyles() {
      // Configure out text
      this.context.font = "normal 12.8px Arial";
      this.context.textBaseline = "middle";
      this.context.textAlign = "center";
    }
    public computeTextDimensions(text: string) {
      // {width: 16.66, actualBoundingBoxLeft: 8.33, actualBoundingBoxRight: 8.66,
      //  actualBoundingBoxAscent: 5.69, actualBoundingBoxDescent: 3.30}
      this.setTextLabelStyles();
      return this.context.measureText(text);
    }

    private drawTextLabel(label) {
      // Draw our box background
      var activeTextLabel = this.viewmodel.activeTextLabel;
      var hover = (label === activeTextLabel);
      var selected = (this.viewmodel.selectedTextLabels && this.viewmodel.selectedTextLabels.includes(label));
      this.setTextLabelStyles();
      var pixelsPerCm = Core.Configuration.data['pixelsPerCm'];
      this.drawRectangle(
        this.viewmodel.convertX(label.x),
        this.viewmodel.convertY(label.y),
        this.viewmodel.convertX(label.x) + label.getWidth() * pixelsPerCm,
        this.viewmodel.convertY(label.y) + label.getHeight() * pixelsPerCm,
        true, /* fill */
        label.background,
        hover, /* stroke */
        label.background,
        textLabelHoverStroke);
      if (selected) {
        // If there's an active wall outside of the multiselect, then show deselect
        var selectedStrokeStyle = label.background;
        if (this.somethingActiveAndNotInSelected()) {
          selectedStrokeStyle = label.background + '66'; // RGB -> RGBA
        }
        var strokeOffset = 2;
        this.drawRectangle(
          this.viewmodel.convertX(label.x) - strokeOffset,
          this.viewmodel.convertY(label.y) - strokeOffset,
          this.viewmodel.convertX(label.x) + label.getWidth() * pixelsPerCm + strokeOffset,
          this.viewmodel.convertY(label.y) + label.getHeight() * pixelsPerCm + strokeOffset,
          false, /* fill */
          null,
          true, /* stroke */
          selectedStrokeStyle,
          2);
      }

      // Draw our text
      this.context.fillStyle = label.color;
      this.context.fillText(label.text,
        this.viewmodel.convertX(label.getCenterX()),
        this.viewmodel.convertY(label.getCenterY()));
    }

    /** */
    private drawLine(startX: number, startY: number, endX: number, endY: number, width: number, color) {
      // width is an integer
      // color is a hex string, i.e. #ff0000
      this.context.beginPath();
      this.context.moveTo(startX, startY);
      this.context.lineTo(endX, endY);
      this.context.lineWidth = width;
      this.context.strokeStyle = color;
      this.context.stroke();
    }

    /** */
    // Clone of `drawPolygon` but without arrays (to avoid memory thrashing)
    private drawRectangle(startX: number, startY: number, endX: number, endY: number, fill, fillColor, stroke?, strokeColor?, strokeWidth?) {
      // fillColor is a hex string, i.e. #ff0000
      fill = fill || false;
      stroke = stroke || false;

      this.context.beginPath();
      this.context.moveTo(startX, startY);
      this.context.lineTo(startX, endY);
      this.context.lineTo(endX, endY);
      this.context.lineTo(endX, startY);
      this.context.closePath();

      if (fill) {
        this.context.fillStyle = fillColor;
        this.context.fill();
      }
      if (stroke) {
        this.context.lineWidth = strokeWidth;
        this.context.strokeStyle = strokeColor;
        this.context.stroke();
      }
    }

    /** */
    private drawPolygon(xArr, yArr, fill, fillColor, stroke?, strokeColor?, strokeWidth?) {
      // fillColor is a hex string, i.e. #ff0000
      fill = fill || false;
      stroke = stroke || false;
      this.context.beginPath();
      this.context.moveTo(xArr[0], yArr[0]);
      for (var i = 1; i < xArr.length; i++) {
        this.context.lineTo(xArr[i], yArr[i]);
      }
      this.context.closePath();
      if (fill) {
        this.context.fillStyle = fillColor;
        this.context.fill();
      }
      if (stroke) {
        this.context.lineWidth = strokeWidth;
        this.context.strokeStyle = strokeColor;
        this.context.stroke();
      }
    }

    /** */
    private drawCircle(centerX, centerY, radius, fillColor) {
      this.context.beginPath();
      this.context.arc(centerX, centerY, radius, 0, 2 * Math.PI, false);
      this.context.fillStyle = fillColor;
      this.context.fill();
    }

    /** returns n where -gridSize/2 < n <= gridSize/2  */
    private calculateGridOffset(n) {
      if (n >= 0) {
        return (n + gridSpacing / 2.0) % gridSpacing - gridSpacing / 2.0;
      } else {
        return (n - gridSpacing / 2.0) % gridSpacing + gridSpacing / 2.0;
      }
    }

    /** */
    private drawGrid() {
      var offsetX = this.calculateGridOffset(-this.viewmodel.originX);
      var offsetY = this.calculateGridOffset(-this.viewmodel.originY);
      var width = this.canvasElement.width;
      var height = this.canvasElement.height;
      for (var x = 0; x <= (width / gridSpacing); x++) {
        this.drawLine(gridSpacing * x + offsetX, 0, gridSpacing * x + offsetX, height, gridWidth, gridColor);
      }
      for (var y = 0; y <= (height / gridSpacing); y++) {
        this.drawLine(0, gridSpacing * y + offsetY, width, gridSpacing * y + offsetY, gridWidth, gridColor);
      }
    }
  }
}